html {
  background-color: rgb(46, 46, 46);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

img {
  max-width: 300px;
  border-radius: 10px;
}

p,
li {
  color: white;
}

label {
  display: none;
}
h3,
h4 {
  color: magenta;
}

.adoptionWindow {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  border: 2px solid green;
}

.adopt-animal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.adoptThis {
  list-style: none;
  margin: 20px;
  padding: 20px;
  border: 2px solid rebeccapurple;
}

.petColumns {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}

.petsList {
  width: 60%;
}

.petsList div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  border: 2px solid magenta;
  margin: 5px;
  padding: 10px;
}

.petsList img {
  width: 100%;
}

.people-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 1000px) {
  .adoptionWindow {
    margin-right: 30%;
  }

  .petColumns {
    margin-right: 30%;
  }

  .people-container {
    position: fixed;
    right: 0;
    top: 0;
    width: 30%;
  }
  
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loadingDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.loadingThing {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(58, 58, 58);
  border: 8px solid red;
  border-top: 8px solid yellow;
  border-right: 8px solid green;
  border-bottom: 8px solid black;
  border-radius: 50%;
  box-shadow: 0px 0px 15px whitesmoke;
  width: 120px;
  height: 120px;
  margin: 20px;
  padding: 5px;
  animation: rotation 02s infinite linear;
}
.loadingThing h3 {
  color: darkslateblue;
}

